/**
 * ----------------------------------------
 * animation scale-up-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@media screen and (max-width:700px) {
    .section__margin {
        margin: 4rem
    }

    .section__padding {
        padding: 4rem;
    }
}

/* tablests and big phones */
@media screen and (max-width:700px) {
    .section__margin {
        margin: 4rem
    }

    .section__padding {
        padding: 4rem;
    }
}

/* Smart phones */
@media screen and (max-width:550) {
    .section__margin {
        margin: 4rem 2rem
    }

    .section__padding {
        padding: 4rem 2rem;
    }
}