.gpt3__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;

    background: var(--gradient-bar);
    border: 1px solid #000000;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 1rem;
}



.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000000;
}

.gpt3__cta-content p {
    font-weight: 500;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}

.gpt3__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-btn button {

    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    min-width: 150px;
    padding: .5rem 1rem;
    border-radius: 40px;
    background: #000000;

    outline: none;
    border: none;
    color: #FFFFFF;
    cursor: pointer;
}


@media screen and (max-width:650px) {
    .gpt3__cta {
        flex-direction: column;
    }

    .gpt3__cta-btn {
        margin: 2rem 0 0;
    }

}

@media screen and (max-width:550px) {
    .gpt3__cta {
        margin: 2rem 4rem
    }

    .gpt3__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .gpt3__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }


}