.gpt3__header {
    display: flex;
}

.gpt3__header_content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;

    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header_content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;

}

.gpt3__header_content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;

    color: var(--color-text);
    margin-top: 1.5rem;

}


.gpt3__header_content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header_content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    background: var(--color-footer);
    border-radius: 5px 0px 0px 5px;
    font-Weight: 400;
    font-size: 20px;
    line-height: 27.32px;
    border: 2px solid var(--color-footer);
    outline: none;
    padding: 0 1rem;
    color: #fff
}

.gpt3__header_content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    background: #FF4820;
    outline: none;
    cursor: pointer;
    color: #FFFFFF;
    border-radius: 0 5px 5px 0;
    padding: 0 1rem;
    border: none;
}

.gpt3__header_content_people {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-top: 2rem;

}

.gpt3__header_content_people img {
    width: 181px;
    height: 38px;
}

.gpt3__header_content_people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #FFFFFF;
    text-align: center;
}


.gpt3__header_image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.gpt3__header_image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header_content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width:650px) {
    .gpt3__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header p {
        font-size: 1rem;
        line-height: 1.5rem;

    }

    .gpt3__header_content_people {
        flex-direction: column;
    }

    .gpt3__header_content_people {
        margin: 0;
    }

    .gpt3__header_content__input input,
    .gpt3__header_content__input button {
        font-size: 1rem;
        line-height: 1.5rem;

    }
}


@media screen and (max-width:490px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p {
        font-size: 14px;
        line-height: 24px;

    }

    .gpt3__header_content__input input,
    .gpt3__header_content__input button {
        font-size: 12px;
        line-height: 16px;

    }

}